/* SLICK SLIDER
================================================== */
/* Slider
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  user-select: none;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
  .slick-slider .slick-track,
  .slick-slider .slick-list {
    transform: translate3d(0, 0, 0);
  }
  .slick-list {
    position: relative;
    display: block;
    overflow: hidden;
    margin: 0;
    padding: 0;
    &:focus {
      outline: none;
    }
    .dragging {
      cursor: pointer;
      cursor: hand;
    }
  }
  .slick-track {
    position: relative;
    top: 0;
    left: 0;
    display: block;
    margin-left: auto;
    margin-right: auto;
    &:before,
    &:after {
      display: table;
      content: '';
    }
    &:after {
      clear: both;
    }
  }
  .slick-slide {
    display: none;
    float: left;
    height: 100%;
    min-height: 1px;
    img {
      display: block;
    }
    &.slick-loading img {
      display: none;
    }
    &.dragging img {
      pointer-events: none;
    }
  }
}

.slick-initialized .slick-slide {
  display: block;
}
.slick-loading {
  .slick-track {
    visibility: hidden;
  }
  .slick-slide {
    visibility: hidden;
  }
  .slick-list {
      background: url(#{$images-path}/Loader/loading.white.gif) center center no-repeat #fff;
  }
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
  display: none;
}



/* Theme
–––––––––––––––––––––––––––––––––––––––––––––––––– */
@charset 'UTF-8';

.slick-prev,
.slick-next {
  font-size: 0;
  line-height: 0;
  position: absolute;
  top: 50%;
  display: block;
  width: 20px;
  height: 20px;
  padding: 0;
  transform: translate(0, -50%);
  color: transparent;
  border: none;
  &:before {
    color: $dim-grey;
    font-size: 20px;
    line-height: 1;
    opacity: .75;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  &:hover,
  &:focus {
    background: transparent;
    &:before {
      opacity: 1;
    }
  }

  &.slick-disabled:before {
    opacity: .25;
  }

}

.slick-prev {
  left: -15px;
  &:before {
    content: '←';
  }
}
.slick-next {
  right: -15px;
  &:before {
    content: '→';
  }
}

.slick-dots {
  display: none;
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
  li {
    position: relative;
    display: inline-block;
    margin: 0 8px;
    button {
      font-size: 0;
      line-height: 0;
      display: block;
      width: 18px;
      height: 18px;
      border-radius: 9px;
      padding: 0;
      color: transparent;
      border: solid 1px $dim-grey;
      background-color: $white;
      opacity: .75;
      &:hover,
      &:focus {
        opacity: 1;
      }
    }
    &.slick-active button {
      opacity: .75;
      background-color: $dim-grey;
    }
  }
  @include breakpoint($tablet) {
    display: block;
  }
}

.slider {
  width: auto;
  margin: 15px;
  .slick-slide {
    background: $white;
    margin: 0 10px;
    &:nth-child(odd) {
      //background: $off-white;
    }
  }
  @include breakpoint($tablet) {
    margin: 15px 20px;
  }
}


/* Marketplace
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.card {
  border: solid 1px lighten($dim-grey, 10);
  border-radius: 6px;
  overflow: hidden;
  a {
    color: inherit;
    &:hover {
      text-decoration: none;
    }
  }
  .card-img {
    overflow: hidden;
    height: 180px;
    position: relative;
    img {
      width: 100%;
    }
    > .metrics {
      display: block;
      position: absolute;
      top: 4px;
      right: 10px;
      border: none;
      .btn-fav {
        padding: 0;
        height: 16px;
        width: 12px;
        border: solid 1px red;
        margin-right: 4px;
      }
      span.score {
        display: inline-block;
        width: 32px;
        height: 32px;
        border-radius: 16px;
        color: $white;
        background: $main-violet;
        text-align: center;
        line-height: 32px;
      }
    }
  }
  .card-body {
    height: auto;
    > div {
      padding: 8px;
      border-bottom: solid 1px $dim-grey;
    }
    > .channel {
      height: 32px;
      .type {
        float: left;
        &[type="Email"]:before {
          content: '';
          height: 16px;
          width: 12px;
          float: left;
          border: solid 1px $dark-grey;
          margin-right: 4px;
        }
      }
      .detail {
        float: right;
        &:before {
          content: '';
          height: 16px;
          width: 12px;
          float: left;
          border: solid 1px $dark-grey;
          margin-right: 4px;
        }
      }
    }
    > .content {
      font-size: 85%;
      height: 180px;
      display: flex;
      flex-flow: column;
      h3 {
        margin-bottom: 8px;
        font-size: 125%;
      }
      h3, span {
        color: $main-blue;
      }
      p {

      }
      .categories {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }
  .card-price {
    padding: 8px;
    .price {
      float: left;
      width: 48px;
      span {
        display: block;
        font-size: 15px;
      }
    }
    .review {
      float: right;
      width: calc(100% - 64px);
      span {
        display: block;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        color: $vivid-blue;
      }
    }
  }
}

.market-title {
  padding: 0 30px;
}
